import { Component, OnInit, OnDestroy } from '@angular/core';
import { Question } from '../../classes/question/question';
import { BroadcastService } from '../../services/broadcast/broadcast.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-broadcast-quiz-questions',
  templateUrl: './broadcast-quiz-questions.component.html',
  styleUrls: ['./broadcast-quiz-questions.component.scss']
})
export class BroadcastQuizQuestionsComponent implements OnInit, OnDestroy {
  public questions: Array<Question> = [];
  private broadcastSubscription: Subscription;

  constructor(
    private broadcastService: BroadcastService
  ) { }

  ngOnInit(): void {
    this.initializeQuestions(this.questions);
  }

  private initializeQuestions(questions: Array<Question>): void {
    questions.push(new Question('What customers might be expecting from your business with Messaging?', 'Question 1'));
    questions.push(new Question('What is the name of the Facebook Marketing Partners that we just introduced to you?', 'Question 2'));
    questions.push(new Question('What is NOT the name of Haravan\'s solutions?', 'Question 3'));

    return;
  }

  public actionHandler(event: Event, payload: string) {
    event.preventDefault();

    this.sendQuestionPayload(payload);
  }

  private sendQuestionPayload(payload): void {
    if (!payload) {
      return alert('Payload cannot be blank!');
    }

    this.broadcastSubscription = this.broadcastService.sendQuestionPayload(payload)
      .subscribe(
        (response): void => {
          if (response['success']) {
            // end send animation here?
            console.log('on success fire');
          }

          return;
        },
        (error): void => {
          // end send animation
          return alert(error.message);
        },
        (): void => {
          console.log('Question payload sent');

          return;
        }
      );
  }

  ngOnDestroy() {
    if (this.broadcastSubscription) {
      return this.broadcastSubscription.unsubscribe();
    }
  }
}
