import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class BroadcastService {
  constructor(
    private http: HttpClient
  ) { }

  public sendQuestionPayload(payload: string): Observable<object> {
    return this.http.post('/api/broadcasts/questions', { payload })
      .pipe(map(data => data));
  }

  public sendBroadcast(message: string): Observable<object> {
    return this.http.post('/api/broadcasts/messages', { text: message })
      .pipe(map(data => data));
  }
}
